* {
  padding: 0;
  margin: 0;
}

.chatbot {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 50vh;
  border: 0.5px solid #fff;
}
.chatbot-messages {
  padding: 1%;
  width: 60%;
  height: 45vh;
  overflow: auto;
}
.chatbot-input {
  width: 100%;
  height: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.message {
  width: 100%;
  color: #191919;
  float: left;
}
.chat-inp {
  width: 60%;
  height: 80%;
  background-color: transparent;
  border: 1px solid #aaa;
  border-radius: 5px;
  padding-left: 10px;
  color: #fff;
}
.chat-btn {
  width: 5%;
  height: 90%;
}
.user {
  padding: 1%;
  background-color: beige;
  border: 1px solid dodgerblue;
  border-radius: 5px;
  float: right;
  text-align: end;
}
.bot {
  padding: 1%;
  background-color: beige;
  border: 1px solid dodgerblue;
  border-radius: 5px;
  float: left;
  text-align: start;
}
